import React, { FC } from "react";
import classNames from "classnames";
import "./Burger.scss";

enum BurgerColor {
  ORANGE = "orange",
  WHITE = "white",
}

type BurgerColorType = "orange" | "white";

export interface IBurgerProps {
  className?: string;
  color?: BurgerColorType;
  isActive?: boolean;
  onClick: (event: React.MouseEvent) => void;
}

export const Burger: FC<IBurgerProps> = ({
  className,
  color,
  isActive,
  onClick,
}) => {
  return (
    <div className={classNames("Burger", className)} onClick={onClick}>
      <div
        className={classNames("Burger", {
          Burger__orange: color === BurgerColor.ORANGE,
          Burger__white: color === BurgerColor.WHITE,
          Burger__active: isActive,
        })}
      />
    </div>
  );
};
