import { FC, memo } from "react";
import { Link } from "react-scroll";
import { useMediaQuery } from "react-responsive";
import Slider from "react-slick";
import isNil from "lodash/isNil";
import clsx from "clsx";

import { Button } from "../../../ui-kit/Button/Button";
import type { TSliderProps } from "../types/types";
import { SLIDER_MAIN_SETTINGS } from "./settings";

import "./MainSlider.scss";
import "../Slider.scss";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const MainSliderComponent: FC<TSliderProps> = props => {
  const { alt = "", width, height, sliderData } = props;
  const settings = SLIDER_MAIN_SETTINGS(props).settings;
  const isMobileScreen = useMediaQuery({ query: "(max-width: 320px)" });
  const defaultImage =
    "https://t3.ftcdn.net/jpg/04/60/01/36/360_F_460013622_6xF8uN6ubMvLx0tAJECBHfKPoNOR5cRa.jpg";
  return !isNil(sliderData) ? (
    <Slider
      {...settings}
      responsive={[
        {
          breakpoint: 769,
          settings: {
            slidesToShow: 1,
            arrows: false,
            dots: true,
          },
        },
      ]}
    >
      {sliderData.map((data, index) => {
        return (
          <div className="SliderMain-Item" key={index}>
            <img
              className="SliderMain-Image"
              height={height}
              width={width}
              src={data.bg}
              alt={alt}
            />
            <div className="SliderMain-Item-Block">
              <h1>{data.title}</h1>
              <p>{data.subtitle}</p>
              <Link to="costCalculator" smooth={true} duration={500}>
                <Button icon={true} text={" "} />
              </Link>
            </div>
          </div>
        );
      })}
    </Slider>
  ) : (
    <img
      className={clsx("SliderMain-Image", {
        "SliderMain-Image__mobile": isMobileScreen,
      })}
      height={height}
      src={defaultImage}
      alt={alt}
    />
  );
};

export const MainSlider = memo(MainSliderComponent);
