import React, { FC, useCallback, useEffect, useMemo } from "react";
import { useSelector } from "react-redux";
import { SubmitHandler, useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import clsx from "clsx";

import { RootState } from "../../redux/store";
import { fetchTariffs } from "../../redux/Slices/tariffsSlice";
import RequestForm from "../RequestForm/RequestForm";
import ThanksForOrder from "../modules/ThanksForOrder/ThanksForOrder";
import useAppDispatch from "../../Hooks/useAppDispatch";
import {
  CostCalculationState,
  setActiveIcon,
  setField,
  setStep,
} from "../../redux/Slices/costCalculationSlise";
import { FormSchema } from "../../utils/zodSchems";
import { Icon } from "../../ui-kit/Icon/Icon";
import { TitleH2 } from "../../ui-kit/Title/TitlesH2";
import { Button } from "../../ui-kit/Button/Button";
import {
  FormField,
  ICalculationFormProps,
} from "../../ui-kit/Input/InputField";

import Man from "../../ui-kit/assets/img/man.png";
import "./CostCalculation.scss";

export type IconType = "truck" | "truckFast" | "airplane" | "train" | "ship";

const CostCalculation: FC<ICalculationFormProps> = () => {
  const dispatch = useAppDispatch();
  const tariffs = useSelector((state: RootState) => state.tariffs.tariffs);
  const step = useSelector((state: RootState) => state.costCalculation.step);

  useEffect(() => {
    dispatch(fetchTariffs());
  }, [dispatch]);

  const costCalculationState = useSelector(
    (state: RootState) => state.costCalculation,
  );

  const getCalculationFormData = (state: CostCalculationState) => ({
    category: state.category,
    weight: state.weight,
    volume: state.volume,
    cost: state.cost,
    insurance: state.insurance,
    customsClearance: state.customsClearance,
    totalCost: state.totalCost,
    deliveryMethod: state.activeIcon,
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
    trigger,
    reset,
  } = useForm<ICalculationFormProps>({
    resolver: zodResolver(FormSchema),
    defaultValues: getCalculationFormData(costCalculationState),
  });

  useEffect(() => {
    reset(getCalculationFormData(costCalculationState));
  }, [costCalculationState, reset]);

  const costFormData = useMemo(
    () => getCalculationFormData(costCalculationState),
    [costCalculationState],
  );

  const onSubmit: SubmitHandler<ICalculationFormProps> = async () => {
    dispatch(setField({ field: "costFormData", value: costFormData, tariffs }));
  };

  const handleNumericFieldChange = (value: string) => {
    const numericValue = parseFloat(value);
    return isNaN(numericValue) ? undefined : numericValue;
  };

  const handleFieldChange = async (
    field: Exclude<
      keyof CostCalculationState,
      "activeIcon" | "step" | "loading" | "error" | "costFormData"
    >,
    value: any,
  ) => {
    const numericFields = ["weight", "cost", "volume", "totalCost"];
    let processedValue = value;

    if (numericFields.includes(field)) {
      processedValue = handleNumericFieldChange(value);
    }
    dispatch(setField({ field, value: processedValue, tariffs }));
  };

  const handleFieldBlur = async (
    field: Exclude<
      keyof CostCalculationState,
      | "activeIcon"
      | "step"
      | "loading"
      | "error"
      | "costFormData"
      | "combinedDataSubmitted"
    >,
  ) => {
    await trigger(field);
  };

  const nextMove = useCallback(async () => {
    const isValid = await trigger();
    if (isValid) {
      dispatch(setStep(step + 1));
    } else {
      console.log(isValid);
    }
  }, [dispatch, step, trigger]);

  const prevMove = useCallback(() => {
    dispatch(setStep(step - 1));
  }, [dispatch, step]);

  return (
    <div className="CostCalculation" id="costCalculator">
      <div className="CostCalculation-Block">
        <TitleH2
          title={"     "}
          color={"green"}
        />

        {step !== 3 && (
          <div className="CostCalculation-Steps">
            {[1, 2, 3].map(num => (
              <span
                key={num}
                className={clsx({
                  "CostCalculation-Steps__active": step >= num,
                })}
              >
                {num}
              </span>
            ))}
          </div>
        )}

        <form
          style={{ display: step === 1 ? "block" : "none" }}
          className="CostCalculation-Form"
          onSubmit={handleSubmit(onSubmit)}
        >
          <div className="CostCalculation-Form__top">
            <div className="CostCalculation-Form__item">
              <label> :</label>
              <select
                className="Input"
                {...register("category")}
                onChange={e => handleFieldChange("category", e.target.value)}
                onBlur={() => handleFieldBlur("category")}
              >
                <option> </option>
                {tariffs.length > 0 ? (
                  tariffs.map(tariff => (
                    <option key={tariff.code} value={tariff.category}>
                      {tariff.category}
                    </option>
                  ))
                ) : (
                  <option disabled> ...</option>
                )}
              </select>
              {errors.category && (
                <p className="error">{errors.category.message}</p>
              )}
            </div>
            <div className="CostCalculation-Form__item">
              <label>  ():</label>
              <FormField
                name={"weight"}
                placeholder={"  "}
                register={register}
                error={errors.weight}
                type={"text"}
                inputMode="numeric"
                onChange={e =>
                  handleFieldChange("weight", parseFloat(e.target.value))
                }
                onBlur={() => handleFieldBlur("weight")}
              />
            </div>
            <div className="CostCalculation-Form__item">
              <label>  ($):</label>
              <FormField
                name={"cost"}
                placeholder={"  "}
                register={register}
                error={errors.cost}
                type={"text"}
                inputMode="numeric"
                onChange={e =>
                  handleFieldChange("cost", parseFloat(e.target.value))
                }
                onBlur={() => handleFieldBlur("cost")}
              />
            </div>
            <div className="CostCalculation-Form__item">
              <label>  (3):</label>
              <FormField
                name={"volume"}
                placeholder={"  "}
                register={register}
                error={errors.volume}
                type={"text"}
                inputMode="numeric"
                onChange={e =>
                  handleFieldChange("volume", parseFloat(e.target.value))
                }
                onBlur={() => handleFieldBlur("volume")}
              />
            </div>
            <div className="CostCalculation-Form__item">
              <label>  ($)</label>
              <FormField
                name={"totalCost"}
                placeholder={" "}
                register={register}
                error={errors.totalCost}
                type={"text"}
                value={costCalculationState.totalCost}
                readOnly
              />
            </div>
            <div className="CostCalculation-Form__checkbox">
              <div className="CostCalculation-Form__checkbox-item">
                <input
                  type="checkbox"
                  {...register("insurance")}
                  onChange={e =>
                    handleFieldChange("insurance", e.target.checked)
                  }
                />
                <label></label>
              </div>
              <div className="CostCalculation-Form__checkbox-item">
                <input
                  type="checkbox"
                  {...register("customsClearance")}
                  onChange={e =>
                    handleFieldChange("customsClearance", e.target.checked)
                  }
                />
                <label> </label>
              </div>
            </div>
          </div>

          <div className="CostCalculation-Form__icons">
            {["truck", "truckFast", "airplane", "train", "ship"].map(icon => (
              <Icon
                key={icon}
                type={icon as IconType}
                className={clsx("Icon", {
                  active: costCalculationState.activeIcon === icon,
                })}
                onClick={() => dispatch(setActiveIcon({ icon, tariffs }))}
              />
            ))}
          </div>

          <Button
            className="CostCalculation-Form__button"
            type="button"
            text={" "}
            icon={true}
            onClick={nextMove}
          />
        </form>

        <div
          className="CostCalculation-Form"
          style={{ display: step === 2 ? "block" : "none" }}
        >
          <RequestForm
            isOpenModal={false}
            prevMove={prevMove}
            nextMove={nextMove}
            userName={""}
            email={""}
            phoneNumber={""}
            comment={""}
          />
        </div>

        <div style={{ display: step !== 3 ? "none" : "block" }}>
          <ThanksForOrder />
        </div>
      </div>
      <img className="CostCalculation-Man" src={Man} alt="" />
    </div>
  );
};
export default CostCalculation;
