import React, { FC } from "react";
import Slider from "react-slick";

import WhyWeItem from "./WhyWeItem";
import useResize from "../../Hooks/useResize";
import { TitleH2 } from "../../ui-kit/Title/TitlesH2";
import { IconType } from "../../ui-kit/Icon/IconType";

import "./WhyWe.scss";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const items: { icon: IconType; text: string }[] = [
  { icon: "file", text: "  " },
  { icon: "speechBubble", text: "  " },
  { icon: "check-mark", text: " " },
  { icon: "like", text: "  " },
  { icon: "magnifying-glass", text: "   " },
  { icon: "credit-card", text: "   " },
  { icon: "delivery-truck", text: "    " },
  {
    icon: "stack",
    text: ", ,     ",
  },
];

const WhyWe: FC = () => {
  const [width] = useResize();

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <div className="WhyWe">
      <TitleH2 title={"    ?"} color={"orange"} />
      {width < 769 ? (
        <Slider {...settings}>
          {items.map(item => (
            <WhyWeItem key={item.icon} icon={item.icon} text={item.text} />
          ))}
        </Slider>
      ) : (
        <div className="WhyWe-Items">
          {items.map(item => (
            <WhyWeItem key={item.icon} icon={item.icon} text={item.text} />
          ))}
        </div>
      )}
    </div>
  );
};

export default WhyWe;
