import React, { useEffect, useState } from "react";
import { Link } from "react-scroll";
import classNames from "classnames";
import { useDispatch, useSelector } from "react-redux";

import { RootState } from "../../redux/store";
import { closeModal, openModal } from "../../redux/Slices/modalSlice";
import RequestForm from "../RequestForm/RequestForm";
import useResize from "../../Hooks/useResize";
import { Icon } from "../../ui-kit/Icon/Icon";
import { Modal } from "../../ui-kit/Modal/Modal";
import { Button } from "../../ui-kit/Button/Button";
import { Burger } from "../../ui-kit/Burger/Burger";

import "./Header.scss";

const Header = () => {
  const dispatch = useDispatch();
  const isOpenModal = useSelector(
    (state: RootState) => state.modals.requestForm,
  );

  const [width] = useResize();

  const [isActive, setIsActive] = useState(false);

  useEffect(() => {
    if (isActive) {
      document.body.classList.add("lock-scroll");
    } else {
      document.body.classList.remove("lock-scroll");
    }
  }, [isActive]);

  const handleToggle = () => {
    setIsActive(prevState => !prevState);
  };
  const handleMenuClick = (event: React.MouseEvent) => {
    event.stopPropagation();
  };
  const handleItemClick = () => {
    setIsActive(false);
  };

  return (
    <div className="Header">
      <div className="Header__Logo">
        <Icon className="Logo" type={"logo"} />
      </div>
      <Burger
        onClick={handleToggle}
        color={isActive ? "orange" : "white"}
        isActive={isActive}
        className="Header-Burger"
      />
      <div
        className={classNames("Header__MenuList", {
          Header__MenuList__open: isActive,
        })}
        onClick={handleMenuClick}
      >
        <ul>
          <Link to="delivery" smooth={true} duration={500}>
            <li onClick={handleItemClick}>  </li>
          </Link>

          <Link to="services" smooth={true} duration={500}>
            <li onClick={handleItemClick}></li>
          </Link>

          <Link to="aboutUs" smooth={true} duration={500}>
            <li onClick={handleItemClick}> </li>
          </Link>
          <Link to="footer" smooth={true} duration={500}>
            <li onClick={handleItemClick}></li>
          </Link>

          <Link to="cooperate" smooth={true} duration={500}>
            <li onClick={handleItemClick}> </li>
          </Link>
        </ul>
        {isActive && (
          <Button
            type={"button"}
            onClick={() => dispatch(openModal("requestForm"))}
            text={" "}
          />
        )}

        <Modal
          onClose={() => dispatch(closeModal("requestForm"))}
          isOpen={isOpenModal}
          className="SliderServices-Modal"
        >
          <Modal.Content>
            <RequestForm
              isOpenModal={true}
              userName={""}
              email={""}
              phoneNumber={""}
              comment={""}
            />
          </Modal.Content>
        </Modal>
      </div>
      {width > 1023 && (
        <Button
          className="Header__Button"
          type={"button"}
          onClick={() => dispatch(openModal("requestForm"))}
          text={" "}
        />
      )}
    </div>
  );
};

export default Header;
