import React, { FC, memo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useMediaQuery } from "react-responsive";
import clsx from "clsx";
import Slider from "react-slick";
import isNil from "lodash/isNil";

import { RootState } from "../../../redux/store";
import { closeModal, openModal } from "../../../redux/Slices/modalSlice";
import RequestForm from "../../RequestForm/RequestForm";
import { TDataSlider, TSliderProps } from "../types/types";
import { Button } from "../../../ui-kit/Button/Button";
import { Icon } from "../../../ui-kit/Icon/Icon";
import { Modal } from "../../../ui-kit/Modal/Modal";
import { SLIDER_DELIVERY_SETTINGS } from "./settings";

import "../Slider.scss";
import "./DeliverySlider.scss";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const DeliverySliderComponent: FC<TSliderProps> = props => {
  const dispatch = useDispatch();

  const isOpenModal = useSelector((state: RootState) => state.modals);

  const [selectedSlide, setSelectedSlide] = useState<TDataSlider | null>(null);

  const { alt = "", width, height, sliderData } = props;
  const settings = SLIDER_DELIVERY_SETTINGS(props).settings;
  const isMobileScreen = useMediaQuery({ query: "(max-width: 320px)" });
  const defaultImage =
    "https://t3.ftcdn.net/jpg/04/60/01/36/360_F_460013622_6xF8uN6ubMvLx0tAJECBHfKPoNOR5cRa.jpg";

  const handleOpenModal = (modalType: string, data: TDataSlider) => {
    setSelectedSlide(data);
    dispatch(openModal(modalType));
  };

  return !isNil(sliderData) ? (
    <>
      <Slider
        {...settings}
        className="SliderDelivery"
        responsive={[
          {
            breakpoint: 1440,
            settings: {
              slidesToShow: 3,
              arrows: true,
              dots: false,
            },
          },
          {
            breakpoint: 1200,
            settings: {
              slidesToShow: 2,
              arrows: false,
              dots: true,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 1,
              arrows: false,
              dots: true,
            },
          },
        ]}
      >
        {sliderData.map((data, index) => {
          return (
            <div className="SliderDelivery-Item" key={index}>
              <img
                className={clsx("SliderDelivery-Image", {
                  "SliderDelivery-Image__mobile": isMobileScreen,
                })}
                height={height}
                width={width}
                src={data.bg}
                alt={alt}
              />
              <div className="SliderDelivery-Block">
                <h3>{data.title}</h3>
                <div className="SliderDelivery-Block__icon">
                  <Icon type={"calendar"} />
                  <p>{data.subtitle}</p>
                  <Icon type={"box"} />
                  <p>{data.anotherSubtitle}</p>
                </div>
                <p>
                   <span>{data.price}</span> $/
                </p>
                <div className="SliderDelivery-Buttons">
                  <Button
                    text={" "}
                    onClick={() => handleOpenModal("requestFormDelivery", data)}
                    className="SliderDelivery-Buttons__small"
                  />
                  <Button
                    onClick={() => handleOpenModal("moreInfoDelivery", data)}
                    text={""}
                    buttonTransparent={true}
                    className="SliderDelivery-Buttons__small"
                  />
                </div>
              </div>
            </div>
          );
        })}
      </Slider>
      {selectedSlide && isOpenModal.requestFormDelivery && (
        <Modal
          onClose={() => dispatch(closeModal("requestFormDelivery"))}
          isOpen={isOpenModal.requestFormDelivery}
          className="SliderServices-Modal"
        >
          <Modal.Header>
            <h3>{selectedSlide.title}</h3>
          </Modal.Header>
          <Modal.Content>
            <RequestForm
              isOpenModal={true}
              userName={""}
              email={""}
              phoneNumber={""}
              comment={""}
            />
          </Modal.Content>
        </Modal>
      )}
      {selectedSlide && isOpenModal.moreInfoDelivery && (
        <Modal
          onClose={() => dispatch(closeModal("moreInfoDelivery"))}
          isOpen={isOpenModal.moreInfoDelivery}
          className="SliderDelivery-Modal"
        >
          <Modal.Content>
            <h3> : {selectedSlide.subtitle}</h3>
            <h3> : {selectedSlide.anotherSubtitle}</h3>
            <p>{selectedSlide.text}</p>
          </Modal.Content>
        </Modal>
      )}
    </>
  ) : (
    <img
      className={clsx("SliderDelivery-Image", {
        "SliderDelivery-Image__mobile": isMobileScreen,
      })}
      height={height}
      src={defaultImage}
      alt={alt}
    />
  );
};

export const DeliverySlider = memo(DeliverySliderComponent);
