import React, { FC } from "react";

interface IFooterItemProps {
  title: string;
  items: string[];
}

const FooterItem: FC<IFooterItemProps> = ({ title, items }) => {
  return (
    <div className="Footer-Content-Item">
      <h3>{title}</h3>
      <ul>
        {items.map((item, index) => (
          <li key={index}>{item}</li>
        ))}
      </ul>
    </div>
  );
};

export default FooterItem;
