import React, { FC, useCallback, useEffect } from "react";
import { useSelector } from "react-redux";
import { SubmitHandler, useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import classNames from "classnames";

import { RootState } from "../../redux/store";
import { submitCombinedForm } from "../../redux/Thunks/costCalculationThunk";
import { CostCalculationState } from "../../redux/Slices/costCalculationSlise";
import { closeModal } from "../../redux/Slices/modalSlice";
import { submitRequestForm } from "../../redux/Thunks/requestFormThunk";
import { Button } from "../../ui-kit/Button/Button";
import { FormField, IRequestFormProps } from "../../ui-kit/Input/InputField";
import { RequestFormSchema } from "../../utils/zodSchems";
import {
  clearFormRequest,
  RequestFormState,
  setField,
} from "../../redux/Slices/requestFormSlice";
import useAppDispatch from "../../Hooks/useAppDispatch";

import "./RequestForm.scss";
import "react-toastify/dist/ReactToastify.css";

export interface IFormProps extends IRequestFormProps {
  prevMove?: () => void;
  nextMove?: () => void;
  isOpenModal?: boolean;
  className?: string | undefined;
}

const RequestForm: FC<IFormProps> = ({
  prevMove,
  nextMove,
  isOpenModal,
  className,
}) => {
  const dispatch = useAppDispatch();

  const { loading, error } = useSelector(
    (state: RootState) => state.requestForm,
  );

  const requestFormState = useSelector((state: RootState) => state.requestForm);
  const costCalculationState = useSelector(
    (state: RootState) => state.costCalculation,
  );

  const getRequestFormData = (state: RequestFormState) => ({
    userName: state.userName,
    phoneNumber: state.phoneNumber,
    email: state.email,
    comment: state.comment,
  });
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitted },
    trigger,
    reset,
    setValue,
  } = useForm<IFormProps>({
    resolver: zodResolver(RequestFormSchema),
    defaultValues: getRequestFormData(requestFormState),
  });

  useEffect(() => {
    reset(getRequestFormData(requestFormState));
  }, [requestFormState, reset]);

  const isFieldsFilled = (
    state: Partial<CostCalculationState>,
    fields: (keyof CostCalculationState)[],
  ): boolean => {
    return fields.every(
      field => state[field] !== null && state[field] !== undefined,
    );
  };

  const onSubmit: SubmitHandler<IFormProps> = async () => {
    const isValid = await trigger();
    if (isValid) {
      const requestFormData = getRequestFormData(requestFormState);
      console.log(
        "RequestForm: onSubmit - Saving requestFormData to Redux",
        requestFormData,
      );
      dispatch(setField({ field: "requestFormData", value: requestFormData }));

      const requiredFields: (keyof CostCalculationState)[] = [
        "category",
        "weight",
        "volume",
        "cost",
      ];
      const isCostCalculationFilled = isFieldsFilled(
        costCalculationState,
        requiredFields,
      );
      if (isCostCalculationFilled) {
        dispatch(submitCombinedForm());
        if (nextMove) {
          nextMove();
        }
      } else {
        dispatch(submitRequestForm(requestFormData)).then(action => {
          console.log("RequestForm: submitRequestForm action", action);
          if (submitRequestForm.fulfilled.match(action)) {
            setTimeout(() => {
              dispatch(clearFormRequest());
              dispatch(closeModal("requestForm"));
              dispatch(closeModal("requestFormDelivery"));
            }, 300);
          }
        });
      }
    }
  };

  const handleClick = useCallback(() => {
    if (prevMove) {
      prevMove();
    }
  }, [prevMove]);
  const handleFieldChange = useCallback(
    async (
      field: Exclude<
        keyof RequestFormState,
        "loading" | "error" | "requestFormData" | "notification"
      >,
      value: any,
    ) => {
      dispatch(setField({ field, value }));
      setValue(field, value);
      await trigger(field);
    },
    [dispatch, setValue, trigger],
  );
  return (
    <div>
      <form
        className={classNames("FormFields", className, {})}
        onSubmit={handleSubmit(onSubmit)}
      >
        <div className="FormFields-Block">
          {isOpenModal && (
            <div className="FormFields-Text">
              <h2> </h2>
              <p>
                 ,   /  
                      .
              </p>
            </div>
          )}
          <div className="FormFields-Field">
            <label>:</label>
            <FormField
              name={"userName"}
              placeholder={"   "}
              register={register}
              error={errors.userName}
              type={"text"}
              value={requestFormState.userName}
              onChange={e => handleFieldChange("userName", e.target.value)}
            />
          </div>
          <div className="FormFields-PhoneEmail">
            <div className="FormFields-Field">
              <label> :</label>
              <FormField
                name="phoneNumber"
                placeholder="+7 (___) ___-__-__ *"
                register={register}
                error={errors.phoneNumber}
                type="tel"
                mask="+7 (999) 999-99-99"
                value={requestFormState.phoneNumber}
                onChange={e => handleFieldChange("phoneNumber", e.target.value)}
              />
            </div>
            <div className="FormFields-Field">
              <label>Email:</label>
              <FormField
                name={"email"}
                placeholder={" E-mail"}
                register={register}
                error={errors.email}
                type={"text"}
                value={requestFormState.email}
                onChange={e => handleFieldChange("email", e.target.value)}
              />
            </div>
          </div>
          <div className="FormFields-Field">
            <label>:</label>
            <FormField
              name={"comment"}
              className={className}
              placeholder={
                "   /,    ,         *"
              }
              register={register}
              error={errors.comment}
              type={"textArea"}
              value={requestFormState.comment}
              onChange={e => handleFieldChange("comment", e.target.value)}
            />
          </div>
        </div>
        {error && <div className="FormFields-Error">{error}</div>}
        <div className="FormFields-Buttons">
          <Button
            className="FormFields-Button"
            disabled={isSubmitted}
            type="submit"
            text={loading ? "..." : " "}
            icon={true}
          />

          {prevMove && (
            <Button
              onClick={handleClick}
              type="button"
              text={""}
              buttonTransparent={true}
            />
          )}
        </div>
      </form>
    </div>
  );
};
export default RequestForm;
