import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import { IRequestFormProps } from "../../ui-kit/Input/InputField";
import { submitRequestForm } from "../Thunks/requestFormThunk";
import {
  showErrorNotification,
  showSuccessNotification,
} from "./notificationSlice";

export interface RequestFormState extends IRequestFormProps {
  loading: boolean;
  error: string | null;
  requestFormData: {};
}

const initialState: RequestFormState = {
  userName: "",
  email: "",
  phoneNumber: "",
  comment: "",
  loading: false,
  error: null,
  requestFormData: {},
};

const requestFormSlice = createSlice({
  name: "requestForm",
  initialState,
  reducers: {
    setField: (
      state,
      action: PayloadAction<{ field: keyof RequestFormState; value: any }>,
    ) => {
      const { field, value } = action.payload;
      (state[field] as any) = value;
    },
    clearFormRequest: state => {
      return initialState;
    },
  },
  extraReducers: builder => {
    builder
      .addCase(submitRequestForm.pending, state => {
        state.loading = true;
        state.error = null;
      })
      .addCase(submitRequestForm.fulfilled, (state, action) => {
        state.loading = false;
        state.requestFormData = action.payload;
        showSuccessNotification(" ");
      })
      .addCase(submitRequestForm.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
        showErrorNotification(
          state.error !== undefined
            ? state.error
            : "   ",
        );
      });
  },
});

export const requestFormReducer = requestFormSlice.reducer;
export const { setField, clearFormRequest } = requestFormSlice.actions;
