import { createAsyncThunk } from "@reduxjs/toolkit";

import { RootState } from "../store";
import { api } from "../../components/modules/Api/api";

export const submitCombinedForm = createAsyncThunk(
  "costCalculation/submitCombined",
  async (_, { getState, rejectWithValue }) => {
    const state = getState() as RootState;
    const costFormData = state.costCalculation.costFormData;
    const requestFormData = state.requestForm.requestFormData;

    const combinedData = {
      requestFormData,
      costFormData,
    };

    console.log("Combined Data:", combinedData); //    

    try {
      const response = await api.post("/Orders", combinedData);
      return response.data;
    } catch (error: any) {
      return rejectWithValue(error.response.data);
    }
  },
);
