import { TSliderProps } from "../types/types";
import { ESliderArrow } from "../SliderArrow/enums";
import SliderArrow from "../SliderArrow/SliderArrow";

export const SLIDER_DELIVERY_SETTINGS = (props: TSliderProps) => {
  const {
    arrows = true,
    dots,
    speed = 500,
    infinity,
    fade = false,
    slidesToShow = 3,
    swipeToSlide = true,
    slideToScroll,
    nextArrow,
    prevArrow,
    className,
    responsive,
  } = props;
  return {
    settings: {
      arrows,
      dots,
      speed,
      infinity,
      fade,
      slideToScroll,
      slidesToShow,
      swipeToSlide,
      nextArrow: arrows ? <SliderArrow type={ESliderArrow.Next} /> : nextArrow,
      prevArrow: arrows ? (
        <SliderArrow type={ESliderArrow.Previos} />
      ) : (
        prevArrow
      ),
      className,
      responsive,
    },
  };
};
