import React, { FC, useState } from "react";
import classNames from "classnames";
import { SlideDown } from "react-slidedown";

import { Icon } from "../Icon/Icon";

import "react-slidedown/lib/slidedown.css";
import "./Accordion.scss";

export interface IFaqProps {
  className?: string;
  title?: string;
  isActive?: boolean;
  children?: React.ReactNode;
}

export const Accordion: FC<IFaqProps> = ({
  title = "",
  isActive = false,
  className,
  children,
}) => {
  const [isOpen, setIsOpen] = useState(isActive);
  const handleToggleAccordion = () => {
    setIsOpen(!isOpen);
  };
  return (
    <div
      className={classNames("Accordion", className, {
        Accordion__active: isOpen,
      })}
    >
      <div className="Accordion-Header" onClick={handleToggleAccordion}>
        <div className="Accordion-HeaderTitle">{title}</div>
        <Icon className="Accordion-HeaderIcon" type={"arrowCircle"} />
      </div>
      <SlideDown className="Accordion-ContentSlideDown">
        {isOpen && <div className="Accordion-Content">{children}</div>}
      </SlideDown>
    </div>
  );
};
