import { z } from "zod";

export const FormSchema = z.object({
  category: z
    .string({
      required_error: " ",
      invalid_type_error: " ",
    })
    .min(1, { message: " " }),

  weight: z
    .number({
      required_error: " ",
      invalid_type_error: " ",
    })
    .min(0.5, { message: " 0.5" })
    .max(1000000, { message: " 1  " }),

  cost: z
    .number({
      required_error: " ",
      invalid_type_error: " ",
    })
    .min(1, { message: " 1$" })
    .max(10000000000, {
      message: " 10 ",
    }),

  volume: z
    .number({
      required_error: " ",
      invalid_type_error: " ",
    })
    .min(0.1, { message: " 0.13" })
    .max(999, { message: " 999 3" }),
});

const nonEmptyString = z.string().refine(data => data.trim() !== "", {
  message: " ",
});

export const RequestFormSchema = z.object({
  email: z.string().email({ message: "  E-mail" }),

  phoneNumber: nonEmptyString.refine(
    data =>
      /^(\+7|7|8)?[\s-]?\(?[1-9][0-9]{2}\)?[\s-]?[0-9]{3}[\s-]?[0-9]{2}[\s-]?[0-9]{2}$/.test(
        data,
      ),
    { message: " " },
  ),

  userName: nonEmptyString.refine(
    data =>
      /(([\p{L}]{1}[\p{L}]{1,40})(\s[\p{L}]{1}[\p{L}]{1,40})?)\s([\p{L}]{1}[\p{L}]{1,40})/u.test(
        data,
      ),
    { message: "   " },
  ),

  comment: z
    .string({
      required_error: " ",
      invalid_type_error: " ",
    })
    .max(300, { message: "  300 " })
    .nonempty(" "),
});
