import { FC, MouseEventHandler, useEffect, useRef } from "react";
import clsx from "clsx";

import { ESliderArrow } from "./enums";
import { Icon } from "../../../ui-kit/Icon/Icon";

import "../Slider.scss";

type TProps = {
  backgroundColor?: string;
  onClick?: MouseEventHandler<HTMLDivElement>;
  opacity?: number;
  type: ESliderArrow;
  className?: string;
  sliderWidth?: string;
};

const SliderArrow: FC<TProps> = ({
  backgroundColor = "#e8e8e8",
  onClick,
  opacity = 1,
  type,
}) => {
  const arrowRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (arrowRef.current) {
      arrowRef.current.style.setProperty(
        "--slider-arrow-backgroundColor",
        backgroundColor,
      );
      arrowRef.current.style.setProperty(
        "--slider-arrow-opacity",
        opacity.toString(),
      );
    }
  }, [opacity, backgroundColor]);
  return (
    <div className="sliderArrow" onClick={onClick} ref={arrowRef}>
      <div
        className={clsx("Slider-ArrowButton", {
          "Slider-ArrowButton__left": type === ESliderArrow.Previos,
          "Slider-ArrowButton__right": type === ESliderArrow.Next,
        })}
      >
        <Icon
          className="Slider-ArrowButton"
          type={
            type === ESliderArrow.Previos
              ? "sliderArrowLeft"
              : "sliderArrowRight"
          }
        />
      </div>
    </div>
  );
};
export default SliderArrow;
