import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import { ITariff } from "./tariffsSlice";
import { ICalculationFormProps } from "../../ui-kit/Input/InputField";
import { submitCombinedForm } from "../Thunks/costCalculationThunk";

export interface CostCalculationState extends ICalculationFormProps {
  activeIcon: string | null;
  step: number;
  loading: boolean;
  error: string | null;
  costFormData: {};
  combinedDataSubmitted: boolean;
}

const initialState: CostCalculationState = {
  category: "",
  weight: null,
  volume: null,
  cost: null,
  insurance: false,
  customsClearance: false,
  totalCost: null,
  activeIcon: "truck",
  step: 1,
  loading: false,
  error: null,
  costFormData: {},
  combinedDataSubmitted: false,
};

const deliveryMethods = {
  truck: { name: "Truck", costPerKg: 1.8 },
  truckFast: { name: "Truck Fast", costPerKg: 2 },
  airplane: { name: "Airplane", costPerKg: 15 },
  train: { name: "Train", costPerKg: 1.5 },
  ship: { name: "Ship", costPerKg: 0.5 },
};

const costCalculationSlice = createSlice({
  name: "costCalculation",
  initialState,
  reducers: {
    setField: (
      state,
      action: PayloadAction<{
        field: keyof CostCalculationState;
        value: CostCalculationState[keyof CostCalculationState];
        tariffs: ITariff[];
      }>,
    ) => {
      const { field, value, tariffs } = action.payload;
      (state[field] as CostCalculationState[typeof field]) = value;
      state.totalCost = calculateTotalCost(state, tariffs);
    },
    setActiveIcon: (
      state,
      action: PayloadAction<{ icon: string | null; tariffs: ITariff[] }>,
    ) => {
      const { icon, tariffs } = action.payload;
      state.activeIcon = icon;
      state.totalCost = calculateTotalCost(state, tariffs);
    },
    setStep: (state, action: PayloadAction<number>) => {
      state.step = action.payload;
    },
    clearFormCalculation: state => {
      return initialState;
    },
    setCombinedDataSubmitted: (state, action) => {
      state.combinedDataSubmitted = action.payload;
    },
  },
  extraReducers: builder => {
    builder
      .addCase(submitCombinedForm.pending, state => {
        state.loading = true;
        state.error = null;
      })
      .addCase(submitCombinedForm.fulfilled, state => {
        state.loading = false;
        state.combinedDataSubmitted = true;
      })
      .addCase(submitCombinedForm.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message ?? "-   ";
      });
  },
});

const calculateTotalCost = (
  state: CostCalculationState,
  tariffs: ITariff[],
) => {
  const deliveryMethod =
    deliveryMethods[state.activeIcon as keyof typeof deliveryMethods];
  if (!deliveryMethod) return 0;

  const weight = state.weight ?? 0;
  const volume = state.volume ?? 0;
  const cost = state.cost ?? 0;

  const density = weight / volume;

  let deliverCost;

  if (density >= 250 || volume === 0) {
    deliverCost = weight * deliveryMethod.costPerKg;
  } else {
    const costPerCubicMeter =
      deliveryMethod.costPerKg * density * Math.log(volume + 1.5);
    deliverCost = volume * costPerCubicMeter;
  }

  const insuranceCost = state.insurance ? cost * 0.03 : 0;
  const customsClearanceCost = state.customsClearance ? 1200 : 0;
  const tariff = tariffs.find(t => t.category === state.category);
  const customsDuty = tariff ? cost * tariff.customsDuty : 0;

  const totalCost =
    customsDuty + deliverCost + insuranceCost + customsClearanceCost;
  if (isNaN(totalCost)) {
    return 0;
  }

  return Math.round(totalCost * 100) / 100;
};

export const {
  setField,
  setActiveIcon,
  setCombinedDataSubmitted,
  clearFormCalculation,
  setStep,
} = costCalculationSlice.actions;

export const costCalculationReducer = costCalculationSlice.reducer;
