import React, { ChangeEvent, FC } from "react";
import { FieldError, UseFormRegister } from "react-hook-form";
import classNames from "classnames";
import ReactInputMask from "react-input-mask";

import "./Input.scss";

export interface IRequestFormProps {
  userName?: string;
  phoneNumber?: string;
  email?: string;
  comment?: string;
}

export interface ICalculationFormProps {
  category?: string;
  weight?: number | null;
  cost?: number | null;
  totalCost?: number | null;
  volume?: number | null;
  customsClearance?: boolean;
  insurance?: boolean;
}

export type CombinedFormProps = IRequestFormProps & ICalculationFormProps;

export type ValidFieldNames =
  | "userName"
  | "phoneNumber"
  | "email"
  | "category"
  | "weight"
  | "cost"
  | "totalCost"
  | "volume"
  | "customsClearance"
  | "insurance"
  | "comment";

export type FormFieldProps = {
  className?: string;
  type: string;
  value?: any;
  onChange?: (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void;
  onBlur?: (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void;
  placeholder?: string;
  name: ValidFieldNames;
  register: UseFormRegister<CombinedFormProps>;
  error: FieldError | undefined;
  mask?: string;
  readOnly?: boolean;
  inputMode?: string;
};

export const FormField: FC<FormFieldProps> = ({
  className,
  type,
  placeholder,
  value,
  name,
  register,
  error,
  onChange,
  onBlur,
  mask,
  readOnly,
}) => {
  const inputProps = {
    className: classNames("Input", className, {
      Input__error: error,
    }),
    type,
    value,
    placeholder,
    ...register(name),
    onChange,
    onBlur,
    readOnly,
  };

  return (
    <>
      {name === "comment" ? (
        <textarea {...inputProps} />
      ) : mask ? (
        <ReactInputMask mask={mask} {...inputProps} />
      ) : (
        <input {...inputProps} />
      )}
      {error && <p className="error">{error.message}</p>}
    </>
  );
};
