import React, { FC, memo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import isNil from "lodash/isNil";
import Slider from "react-slick";
import clsx from "clsx";
import { useMediaQuery } from "react-responsive";

import { RootState } from "../../../redux/store";
import { closeModal, openModal } from "../../../redux/Slices/modalSlice";
import { Button } from "../../../ui-kit/Button/Button";
import { Modal } from "../../../ui-kit/Modal/Modal";
import { SLIDER_SERVICES_SETTINGS } from "./settings";
import { TDataSlider, TSliderProps } from "../types/types";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./ServicesSlider.scss";
import "../Slider.scss";

const ServicesSliderComponent: FC<TSliderProps> = props => {
  const dispatch = useDispatch();
  const isOpenModal = useSelector((state: RootState) => state.modals);

  const [selectedSlide, setSelectedSlide] = useState<TDataSlider | null>(null);

  const handleOpenModal = (modalType: string, data: TDataSlider) => {
    setSelectedSlide(data);
    dispatch(openModal(modalType)); //   
  };
  const { alt = "", width, height, sliderData } = props;
  const settings = SLIDER_SERVICES_SETTINGS(props).settings;
  const isMobileScreen = useMediaQuery({ query: "(max-width: 320px)" });
  const defaultImage =
    "https://t3.ftcdn.net/jpg/04/60/01/36/360_F_460013622_6xF8uN6ubMvLx0tAJECBHfKPoNOR5cRa.jpg";
  return !isNil(sliderData) ? (
    <>
      <Slider
        {...settings}
        className="SliderServices"
        responsive={[
          {
            breakpoint: 1440,
            settings: {
              slidesToShow: 3,
              arrows: true,
              dots: false,
            },
          },
          {
            breakpoint: 1200,
            settings: {
              slidesToShow: 2,
              arrows: false,
              dots: true,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 1,
              arrows: false,
              dots: true,
            },
          },
        ]}
      >
        {sliderData.map((data, index) => {
          return (
            <div className="SliderServices-Item" key={index}>
              <img
                className={clsx("SliderServices-Image", {
                  "SliderServices-Image__mobile": isMobileScreen,
                })}
                height={height}
                width={width}
                src={data.bg}
                alt={alt}
              />
              <div className="SliderServices-Item-Block">
                <h3>{data.title}</h3>
                <p>{data.subtitle}</p>
                <div className="SliderServices-Item-Buttons">
                  <Button
                    onClick={() => handleOpenModal("moreInfoServices", data)}
                    text={""}
                    buttonTransparent={true}
                    className="SliderServices-Item-Buttons__small"
                  />
                </div>
              </div>
            </div>
          );
        })}
      </Slider>
      {selectedSlide && (
        <Modal
          onClose={() => dispatch(closeModal("moreInfoServices"))}
          isOpen={isOpenModal.moreInfoServices}
          className="SliderServices-Modal"
        >
          <Modal.Header>
            <h2>{selectedSlide.title}</h2>
          </Modal.Header>
          <Modal.Content>
            <h3>{selectedSlide.subtitle}</h3>
            <p>{selectedSlide.text}</p>
          </Modal.Content>
        </Modal>
      )}
    </>
  ) : (
    <img
      className={clsx("SliderServices-Image", {
        "SliderServices-Image__mobile": isMobileScreen,
      })}
      height={height}
      src={defaultImage}
      alt={alt}
    />
  );
};
export const ServicesSlider = memo(ServicesSliderComponent);
