import React, { ReactElement, useEffect, useState } from "react";
import { default as ReactModal } from "react-responsive-modal";
import classNames from "classnames";

import useResize from "../../Hooks/useResize";
import { Icon } from "../Icon/Icon";
import { Button } from "../Button/Button";

import "react-responsive-modal/styles.css";
import "./Modal.scss";

export type IModalSize = "sm" | "md" | "lg";

export interface IModalProps {
  className?: string;
  children?: React.ReactNode;
  size?: IModalSize;
  isOpen: boolean;
  onClose: () => void;
}

export const Modal = ({
  className,
  children,
  size = "lg",
  isOpen,
  onClose,
}: IModalProps): ReactElement => {
  const [width] = useResize();
  const [currentSize, setCurrentSize] = useState<IModalSize>(size);

  useEffect(() => {
    if (width <= 469) {
      setCurrentSize("sm");
    } else if (width <= 769) {
      setCurrentSize("md");
    } else if (width <= 1024) {
      setCurrentSize("lg");
    } else {
      setCurrentSize("lg");
    }
  }, [width]);

  useEffect(() => {
    const scrollbarWidth =
      window.innerWidth - document.documentElement.clientWidth;
    if (isOpen && scrollbarWidth) {
      const _style = {
        modal: { marginRight: `${scrollbarWidth + 16}px` },
      };
      setStyles(_style);
      document.body.classList.add("Modal__open");
      document.body.style.paddingRight = `${scrollbarWidth}px`;

      return () => {
        setStyles({});
        document.body.classList.remove("Modal__open");
        document.body.style.removeProperty("padding-right");
      };
    }
  }, [isOpen]);
  const defaultClassName = {
    modal: classNames("ModalDefault", {
      ModalDefault__small: currentSize === "sm",
      ModalDefault__medium: currentSize === "md",
      ModalDefault__large: currentSize === "lg",
    }),
    closeButton: classNames("ModalDefault-CloseButton"),
    className,
  };

  const [styles, setStyles] = useState({});
  return (
    <div>
      <ReactModal
        classNames={defaultClassName}
        center
        closeIcon={<Icon type={"close"} />}
        styles={styles}
        open={isOpen}
        onClose={onClose}
      >
        <div className="Modal">{children}</div>
      </ReactModal>
    </div>
  );
};

export interface IModalHeaderProps {
  className?: string;
  children?: React.ReactNode;
  align?: "left" | "center" | "right";
  img?: string;
}

Modal.Header = ({
  className,
  children,
  align,
}: IModalHeaderProps): ReactElement => {
  return (
    <div
      className={classNames("ModalHeader", className, {
        ModalHeader__left: align === "left",
        ModalHeader__center: align === "center",
        ModalHeader__right: align === "right",
      })}
    >
      {children}
    </div>
  );
};

export interface IModalContentProps {
  className?: string;
  children?: React.ReactNode;
  img?: string;
}

Modal.Content = ({ className, children, img }: IModalContentProps) => {
  return (
    <div className={classNames("ModalContent", className, {})}>
      {img}
      {children}
    </div>
  );
};

export interface IModalFooterProps {
  className?: string;
  buttonText?: string;
  onSubmit?: () => void;
}

Modal.Footer = ({ className, onSubmit, buttonText }: IModalFooterProps) => {
  return (
    <div className={classNames("ModalFooter", className, {})}>
      <Button onClick={onSubmit} type={"submit"} text={buttonText} />
    </div>
  );
};
