import React from "react";
import { useDispatch } from "react-redux";

import { clearFormRequest } from "../../../redux/Slices/requestFormSlice";
import {
  clearFormCalculation,
  setStep,
} from "../../../redux/Slices/costCalculationSlise";
import { Icon } from "../../../ui-kit/Icon/Icon";
import { Button } from "../../../ui-kit/Button/Button";

import "./ThanksForOrder.scss";

const ThanksForOrder = () => {
  const dispatch = useDispatch();
  const handleClearStore = () => {
    dispatch(clearFormCalculation());
    dispatch(clearFormRequest());
    dispatch(setStep(1));
  };

  return (
    <div className="ThanksForOrder">
      <div className="ThanksForOrder-Content">
        <Icon type={"check-mark-big"} className="ThanksForOrder-Icon" />
        <h2>  !</h2>
        <p>    .</p>
        <Button text={" "} onClick={handleClearStore} />
      </div>
    </div>
  );
};

export default ThanksForOrder;
