import { configureStore } from "@reduxjs/toolkit";
import storage from "redux-persist/lib/storage";
import { persistReducer, persistStore } from "redux-persist";
import { combineReducers } from "redux";

import { modalReducer } from "./Slices/modalSlice";
import { costCalculationReducer } from "./Slices/costCalculationSlise";
import { tariffsReducer } from "./Slices/tariffsSlice";
import { requestFormReducer } from "./Slices/requestFormSlice";
import { notificationReducer } from "./Slices/notificationSlice";

const persistConfig = {
  key: "root",
  storage,
  whitelist: ["costCalculation", "requestForm"],
};

const rootReducer = combineReducers({
  modals: modalReducer,
  costCalculation: costCalculationReducer,
  tariffs: tariffsReducer,
  requestForm: requestFormReducer,
  notification: notificationReducer,
});

const persistedReducer = persistReducer(persistConfig, rootReducer);

export const store = configureStore({
  reducer: persistedReducer,
  middleware: getDefaultMiddleware =>
    getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: [
          "submitCombinedForm/pending",
          "submitCombinedForm/fulfilled",
          "submitCombinedForm/rejected",
          "submitRequestForm/pending",
          "submitRequestForm/fulfilled",
          "submitRequestForm/rejected",
        ],
      },
    }),
});

export const persistor = persistStore(store);

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
