import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

import { api } from "../../components/modules/Api/api";

export interface ITariff {
  category: string;
  code: string;
  customsDuty: number;
}

interface TariffsState {
  tariffs: ITariff[];
  status: "idle" | "loading" | "succeeded" | "failed";
  error: string | null;
}

const initialState: TariffsState = {
  tariffs: [],
  status: "idle",
  error: null,
};

export const fetchTariffs = createAsyncThunk(
  "tariffs/fetchTariffs",
  async () => {
    const response = await api.get("/PriceList");
    return response.data;
  },
);

const tariffsSlice = createSlice({
  name: "tariffs",
  initialState,
  reducers: {},
  extraReducers: builder => {
    builder
      .addCase(fetchTariffs.pending, state => {
        state.status = "loading";
        state.error = null;
      })
      .addCase(fetchTariffs.fulfilled, (state, action) => {
        state.status = "succeeded";
        state.tariffs = action.payload;
      })
      .addCase(fetchTariffs.rejected, (state, action) => {
        state.status = "failed";
        if (action.error.message) {
          state.error = action.error.message ?? "-   ";
        }
      });
  },
});
export const tariffsReducer = tariffsSlice.reducer;
