import React, { FC, ReactNode } from "react";
import classNames from "classnames";

import "./TitleH2.scss";

export interface ITitle {
  title?: string;
  color?: "green" | "orange";
  className?: string;
  children?: ReactNode;
}

export const TitleH2: FC<ITitle> = ({ title, color, className, children }) => {
  return (
    <div className={classNames("Title", className)}>
      <h2
        className={classNames("Title-Item", className, {
          Title_green: color === "green",
          Title_orange: color === "orange",
        })}
      >
        {title}
      </h2>
      {children}
    </div>
  );
};
